import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from './components/jsx/home'
import Navbar from './components/jsx/navbar'
import Donation from './components/jsx/donation'
import DonationMain from "./components/jsx/donationMain";
import GallarySection from './components/jsx/gallary-section'
import Tour from './components/jsx/tour'
import Pooja from './components/jsx/pooja'
import Blog from './components/jsx/blog'
import Text from './components/jsx/text'
import VisitorGuide from "./components/jsx/visitorGuide";
import MapContainer from "./components/jsx/map";
import data from "./components/jsx/faq-com";
import About from "./components/jsx/about"
import ShortReview from './components/jsx/short-review'
import CardCartoon from './components/jsx/card-cartoon'
import AboutUs from './components/jsx/about-us'
import Course from './components/jsx/course'
import FAQ from "./components/jsx/faq-com";
import Footer from './components/jsx/footer'
import ContactUs from './components/jsx/contact-us'
import FooterTwo from './components/jsx/footer-two'

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path='/navbar' exact component={Navbar} />
          <Route path='/' exact component={Home} />
          <Route path='/donation' exact component={Donation} />
          <Route path='/donation-main' exact component={DonationMain} />
          <Route path='/gallary' exact component={GallarySection} />
          <Route path='/tour' exact component={Tour} />
          <Route path='/blog' exact component={Blog} />
          <Route path='/text' exact component={Text} />
          <Route path='/faq' exact component={FAQ} />
          <Route path='/faq' exact component={data} />
          <Route path='/guide' exact component={VisitorGuide} />
          <Route path='/pooja' exact component={Pooja} />
          <Route path='/about' exact component={About} />
          <Route path='/card' exact component={CardCartoon} />
          <Route path='/contact' exact component={ContactUs} />
          <Route path='/map' exact component={MapContainer} />
          <Route path='/about-us' exact component={AboutUs} />
          <Route path='/course' exact component={Course} />
          <Route path='/Short-review' exact component={ShortReview} />

          <Route path='/footer' exact component={Footer} />
          <Route path='/footer-second' exact component={FooterTwo} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;